class regForm {
    constructor() {
        this.validFormReg = {
            // 用户名
            nickName: {
                reg: /^[a-zA-Z0-9_]{4,12}$/,
                msg: "用户名格式不正确"
            },
            // 手机号
            phone: {
                reg: /^1[3-9]\d{9}$/,
                msg: "手机号格式不正确"
            },
            // 密码
            password: {
                reg: /^\w{6,16}$/,
                msg: "密码格式不正确"
            },
        }
    }
    // 表单验证
    validForm(obj,self){
        for(let key in obj){
            // 判断验证是否通过
            if(!this.validFormReg[key].reg.test(obj[key])){
                // 若验证不通过则显示提示框
                self.$toast(this.validFormReg[key].msg);
                // 终止判断
                return false;
            }
        }
        // 验证通过
        return true;
    }
}

export default new regForm();