<template>
  <div class="register">
    <van-nav-bar title="注册" left-text="登录" right-text="关闭" @click-left="goPage('Login')" @click-right="goPage('My')"/>
    <div class="register-box">
      <van-form @submit="onSubmit">
        <van-field
          type="text"
          label="昵称"
          placeholder="数字字母_组成(4-12位)"
          v-model="userMsg.nickName"
          maxlength="12"
        >
          <!-- 左边图标 -->
          <van-icon class="register-icon fa fa-user" slot="left-icon" />
        </van-field>
        <van-field
          type="text"
          label="手机号"
          placeholder="11位手机号"
          v-model="userMsg.phone"
          maxlength="11"
        >
          <van-icon class="register-icon fa fa-phone" slot="left-icon" />
        </van-field>
        <van-field
          :type="isShow?'password':'text'"
          label="密码"
          placeholder="数字字母_组成(6-16位)"
          maxlength="16"
          v-model="userMsg.password"
          @click-right-icon="isToggleShow"
        >
          <van-icon class="register-icon fa fa-lock" slot="left-icon" />
          <!-- fa-eye 显示密码图标-->
          <!-- fa-eye-slash 隐藏密码图标-->
          <van-icon
            class="register-icon fa"
            slot="right-icon"
            :class="[isShow?'fa-eye-slash':'fa-eye']"
          />
        </van-field>
        <div style="margin: 16px;">
          <van-button class="register-btn" round block type="info" native-type="submit">同意协议并注册</van-button>
        </div>
      </van-form>
    </div>
    <div class="logo">
      <img class="auto-img logoImg" src="../assets/images/logo.png" alt />
    </div>
    <p class="relus">
      已阅读并同意以下协议
      <a href="javascript:;">xx平台服务协议</a>。
      <a href="javascript:;">隐私权政策</a>、
      <a href="javascript:;">法律声明</a>、
      <a href="javascript:;">xx及客户端服务协议</a>
    </p>
  </div>
</template>

<script>
import regForm from "../assets/js/regForm";
import "../assets/css/register.less";
export default {
  data() {
    return {
      userMsg: {
        phone: "",
        nickName: "",
        password: ""
      },
      isShow: true
    };
  },
  methods: {
    // 改变密码框输入类型
    isToggleShow() {
      this.isShow = !this.isShow;
    },
    // 点击提交按钮 判断验证是否通过并提交数据
    onSubmit() {
      // /^[a-zA-Z0-9_]{4,12}$/; 用户名正则
      // /^1[3-9]\d{9}$/;  手机号正则
      // /^\w{6,16}$/;  密码正则
      if (regForm.validForm(this.userMsg, this)) {
        // 复制对象
        let data = Object.assign({}, this.userMsg);
        // 请求秘钥
        data.appkey = this.appkey;
        // 加载提示框
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          // 延迟关闭加载框时间 为0则不自动关闭
          duration: 0
        });
        // 发起注册请求
        this.axios({
          method: "post",
          url: "/register",
          data
        })
          .then(result => {
            // 关闭加载框
            this.$toast.clear();
            
            if(result.data.code == 100){
              this.$router.push({name:"Login"});
            }else{
              this.$toast(result.data.msg)
            }
          })
          .catch(err => {
            // 关闭加载框
            this.$toast.clear();
            
          });
      }
    },
    // 跳转页面
    goPage(name) {
      this.$router.push({ name: name });
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar__text {
  color: slateblue;
  border-left: 1px dashed #eee;
  border-top: 1px dashed #eee;
  border-radius: 13px;
  padding: 2px 8px;
  box-shadow: 2px 2px 2px #ddd;
  line-height: 25px;
}
</style>